
body {
  font-family: Arial, sans-serif;
  background-color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.calculator-container {
  background-color: #fff;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  width: 300px;
  text-align: center;
}

.calculator-container h2 {
  color: #333;
}

input, select, button {
  font-size: 16px;
  padding: 10px;
  margin: 5px 0;
  width: 100%;
  box-sizing: border-box;
  border: 1px solid #0c0c0c;
  border-radius: 4px;
}

button {
  background-color: #cc0909;
  color: rgb(255, 255, 255);
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0d6268;
}

.result {
  font-size: 20px;
  margin-top: 15px;
  color: #333;
}
